 .buttons svg {
     height: 30px;
     cursor: pointer;
     color: var(--dark-grey);
     font-size: 35px;
 }

 svg:hover {
     -webkit-transform: scale(1.3);
         -ms-transform: scale(1.3);
             transform: scale(1.3);
 }

 img {
     height: 22rem;
     width: 21rem;
     -o-object-fit: cover;
        object-fit: cover;
     margin-left: 10px;
     border-radius: 5%;
 }

 .buttons {
     bottom: 3rem;
 }


