.about-container {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    overflow: hidden;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    width: 100%;
}

.about-container img {
    -webkit-box-shadow: 2rem 2rem 2rem black;
            box-shadow: 2rem 2rem 2rem black;
    border-radius: 10px;
    width: 300px;
}

.about-container p {
    color: var(--dark-grey);
    font-size: 22px;
    line-height: 35px;
}

.about-container h2 {
    color: var(--off-white);
    font-size: 35px;
}

.about-container a {
    text-decoration: none;
}

.about-container a:hover {
    text-decoration: underline;
}

.internal-link {
    text-decoration: none;
}
.internal-link:hover {
    text-decoration: underline;
}

.text:hover {
    color: var(--off-white);
    -o-transition: all .4s ease;
    transition: all .4s ease;
    -webkit-transition: all .4s ease;
}

.second-text:hover {
    color: var(--off-white);
    -o-transition: all .4s ease;
    transition: all .4s ease;
    -webkit-transition: all .4s ease;
}


